
<style lang="less" scoped>
  @import url('../../assets/css/workArea.less');
  #searchBar {
    position: sticky;
    top: 0px;
    display: flex;
    height: 70px;
    background-color: #fff;
    width: 100%;
    div {
      flex: 2;
      padding-left: 20px;
      height: 60px;
      vertical-align: middle;
      background-color: #fff;
      img {
        width: 30px;
        height: 30px;
        vertical-align: middle;
      }
    }
    span {
      background-color: #fff;
      flex: 8;
      height: 60px;
      text-indent: 3em;
      vertical-align: middle;
      line-height: 60px;
    }
  } // 头部
  .work_content {
    background-color: #fff;
    padding: 80px 0 20px 0;
    .work_header {
      height: 80px;
      line-height: 80px;
      p:nth-child(1) {
        float: left;
        padding-left: 25px;
        font-weight: bold;
        font-size: 30px;
      }
    }
    .middle_content {
      margin-top: 60px;
      &>p {
        padding: 0  25px;
        height: 100%;
        font-size: 30px;
        font-weight: 500;
        box-sizing: border-box;
        img {
          width: 100%;
          height: auto;
        }
      }
      &>p:nth-child(1) {
        height: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        &>span{
          flex: 1;
          font-size: 30px;
          &:nth-child(2){
            text-align: right;
            &>b{
              font-size: 30px;
              color: #3eb140;
            }
          }
        }
      }
      &>p:nth-child(2){
        height: 250px;
        width: 85%;
        border-radius: 10px;
        overflow: hidden;
        margin: 0 auto;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
      }
      &>div{
        width: 93%;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
        position: relative;
        &>p{
          width: 33%;
          height: 250px !important;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center center;
          padding: 3px;
          box-sizing: border-box;
        }
        &>span{
          display: inline-block;
          position: absolute;
          bottom: 30px;
          right: 30px;
          border-radius: 20px;
          background-color: rgba(0, 0, 0, .4);
          color: #fff;
          text-align: center;
          height: 40px;
          line-height: 40px;
          font-size: 30px;
        }
      }
    }
    .work_bottom {
      padding: 0 25px;
      p {
        margin: 10px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        span:nth-child(1) {
          flex: 2;
          color: #292929;
          margin-right: 30px;
          font-size: 30px;
          text-align: right;
        }
        span:nth-child(2) {
          flex: 8;
          color: #292929;
          font-size: 30px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }
      }
    }
  }
  .workSubmitBtns {
    width: 100%;
    text-align: center;
    position: fixed;
    bottom: 100px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    &>button {
      display: inline-block;
      width: 550px;
      height: 88px;
      border: none;
      outline: none;
      color: #fff;
      font-size: 30px;
      margin: 20px 0;
      border-radius: 50px;
    }
    &>button.submitedBtn {
      background-color: rgba(78, 224, 72, 0.8);
    }
    &>button.editBtn {
      background-color: #ff644e;
    }
  }
  // previewer
  .swiper-slide.work-range-detail {
    background-position: center center;
    background-size: cover;
  }
  // previewer
  .preview-container {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.9);
    height: 100vh;
  }
  .swiper-slide.previewer {
    width: 100%;
    min-height: 100vh;
    & > img {
      width: 100vw;
      height: auto;
    }
  }
</style>
<template>
  <div class="container">
    <mt-header fixed title="作业要求">
      <router-link to="" slot="left">
        <mt-button icon="back" @click="$router.back(-1)"></mt-button>
      </router-link>
    </mt-header>

    <div class="work_content recommend" v-if="task">
      <div class="work_header">
        <p>{{task.title}}</p>
      </div>
      <div class="work_bottom" v-if="task && task.liveClassId">
        <p><span>课程名称:</span><span>{{task.expectName}}期</span></p>
        <p><span>讲师:</span><span>{{task.user.nickname}}</span></p>
        <p><span>作业要求:</span><span v-html="task.content"></span></p>
        <p><span>授课时间:</span><span>{{task.liveClassId | dateFm}}</span></p>
        <p><span>截止日期:</span><span>{{task.startdate | dateFm}}</span></p>
      </div>
      <div class="middle_content">
        <p><span>作业案例图</span><span>已提交: <b>{{task.totaljob}}</b>份</span></p>
        <Photoswipe>
          <p 
            v-if="task.coverImgSamll && task.coverImgSamll.split(',').length === 2"
            :data-pswp-src="task.coverImgSamll.split(',')[0]"
            :key="`bg-${1}`"
            :style="getImageItemStyle(task.coverImgSamll.split(',')[0])"
            >
          </p>
          <div v-if="task.coverImgSamll && task.coverImgSamll.split(',').length > 1">
            <p 
              v-if="src !== '' && index <= 5 && index > 2" 
              v-for="(src, index) in task.coverImgSamll.split(',')" 
              :data-pswp-src="src"
              :key="`bg-${index}`"
              :style="getImageItemStyle(src)"
              >
              </p>
            <span v-if="task.coverImgSamll.split(',').length - 6 >= 1">{{task.coverImgSamll.split(',').length - 6}}</span>
          </div>
        </Photoswipe>
      </div>
      <div class="workSubmitBtns">
        <button v-if="LOGIN_USER_OBJECT && task.jobStatu == 'UNAPPROVED'  && task.level === '0'" class="editBtn" @click="editWork(task)">修改作业</button>
        <button disabled v-if="LOGIN_USER_OBJECT && task.jobStatu == 'APPROVED' && task.level !== '0'" class="editBtn">已提交作业</button>
        <button v-if="LOGIN_USER_OBJECT && task.jobStatu == 'UNSUBMITED'" class="submitedBtn" @click="submitWork(task)">提交作业</button>
        <button disabled v-if="LOGIN_USER_OBJECT && task.jobStatu == 'UNAPPROVED' &&  task.level !== '0'" class="submitedBtn">作业批改中</button>
      </div>
    </div>

    <work-area-item v-if="workList && workList.length > 0" :areaList="workList"></work-area-item>

    <login v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>

    <previewer v-if="showPreViewer" :previewerArr="previewerArr" @closePreview="closePreview"></previewer>
  </div>
</template>
<script>
  import { createNamespacedHelpers } from 'vuex'
  import workAreaItem from '@/components/workAreaItem'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
  import Vue from 'vue'
  import VueLazyload from 'vue-lazyload'
  import login from '@/components/login'
  import previewer from '@/components/previewer'
  
  Vue.use(VueLazyload, {
    error: require('../../assets/img/default_blue.png'),
    loading: require('../../assets/img/Loading4.gif')
  })
  
  export default {
    components: {
      login,
      workAreaItem,
      previewer
    },
    data() {
      return {
        task: {
          user: {}
        },
        page: 1, // 分页
        pageSize: 5, // 当前分页大小
        workList: [],
        pagination: true,
        showLoginModal: 0,
        showPreViewer: 0,
        previewerObj: null,
        previewerArr: []
      }
    },
    methods: {
      getImageItemStyle(src) {
        return {
            backgroundImage: `url(${src})`,
            backgroundPosition: 'center',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
        }
      },
      //调用store中的action
    ...mapActions([
      'TASK_DETAILS',
      'GET_JOBLIST_BY_TASKID'
    ]),
      // 关闭预览
      closePreview(info) {
        this.showPreViewer = 0
        this.previewerObj = null
      },
      // 打开预览
      openPreviewer(item, index) {
        if(item.length === 2 && item[item.length - 1] === ''){
          this.showPreViewer = 1
          this.previewerArr = item
          this.$nextTick(() => {
          let _this = this
          this.previewerObj = new Swiper("#previewer", {
              on: {
                slideChangeTransitionEnd() {}
              }
            })
          })
          return
        }
        let tag = item[index]
        item.splice(index, 1)
        item.unshift(tag)
        this.previewerArr = item
        this.showPreViewer = 1

        this.$nextTick(() => {
          let _this = this
          this.previewerObj = new Swiper("#previewer", {
            on: {
              slideChangeTransitionEnd() {}
            }
          })
        })
      },
      // 子级组件自定义事件
      showDialogEvent(data) {
        this.showLoginModal = data
      },
      // 加载分页
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return;
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1
          this.axios.get('/api/getJobListByTaskid', {
            params: {
              taskId: this.$route.params.taskId,
              page: this.page,
              pageSize: this.pageSize
            }
          }).then( data => {
            if (this.pagination) {
              if (data.data.list.length > 0) {
                this.workList = this.workList.concat(data.data.list)

                if(this.workList.length > 0 && data.data.list.length > 0){
                    for (let index = 0; index < data.data.list.length; index++) {
                        if(JSON.stringify(this.workList).indexOf(JSON.stringify(data.data.list[index])) === -1){
                            this.workList.push(data.data.list[index])
                        }
                    }
                }else{
                    this.workList = data.data.list
                }

                this.$Indicator.close()
              } else {
                this.$Toast({
                  message: '没有更多了',
                  position: 'middle',
                  duration: 1000
                })
                this.$Indicator.close()
                this.pagination = false
              }
            }
          }).catch( err => {
            console.log(err)
          })
        }
      },
      // 编辑作业
      editWork(task) {
        this.$router.push({
          name: 'publishWork',
          params: {
            taskId: task.taskId,
            statu: task.jobStatu
          }
        });
      },
      // 提交作业
      submitWork(task) {
        this.$router.push({
          name: 'publishWork',
          params: {
            taskId: task.taskId,
            statu: task.jobStatu
          }
        });
      },
      //作品文字信息
      workDetailFetch(){
        this.TASK_DETAILS({
          taskId: this.$route.params.taskId
        }).then(data => {
          this.task = data.data
        }).catch(err => {
          console.log(err)
        })
      },
      //相关案例
      workDataList(){
        this.GET_JOBLIST_BY_TASKID({
            taskId: this.$route.params.taskId,
            page: this.page,
            pageSize: this.pageSize
        }).then( data => {
          if(data.data.list && data.data.list.length > 0){
            this.workList = data.data.list
          }
        }).catch( err => {
          console.log(err)
        })
      },
    },
    created() {
      
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      Promise.all([this.workDataList(), this.workDetailFetch()]).then(() => {
        this.$Indicator.close()
        window.onscroll = () => {
          this.dropDown()
        }
      })
    },
    computed: {
      ...mapState([
        'LOGIN_USER_OBJECT'
      ])
    },
    watch: {
      LOGIN_USER_OBJECT(state){
        if(state){

          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })

          this.page = 1

          Promise.all([this.workDataList(), this.workDetailFetch()]).then(() => {
            this.$Indicator.close()
            window.onscroll = () => {
              this.dropDown()
            }
          })
        }
      }
    }
  }
</script>



